@media (min-width:1200px) {
  .container {
    max-width: 1240px !important;
  }
}
@media (max-width:1200px) {
  .navbar-dark .navbar-nav .nav-link {
    font-size: 14px;
  }

  .btn-primary {
    font-size: 14px;
    padding: 10px 20px;
  }
}

@media (max-width:1024px) {

  .hero-section h1,
  .text-slider h1 {
    font-size: 40px;
    font-weight: 900;
  }
}

@media (max-width: 991px) {
  .navbar-collapse {
    position: fixed;
    top: 50px;
    left: 0;
    padding-left: 15px;
    padding-right: 15px;
    padding-bottom: 15px;
    width: 77%;
    height: 100%;
  }
  .tabs > ul {
    flex-wrap: nowrap;
    white-space: nowrap;
    overflow: auto;
  }
  .navbar-dark .navbar-nav .nav-link.active {
    width: fit-content;
  }
  .navbar {
    padding: 0.5rem 0px;
  }
  .navbar-collapse.collapsing {
    left: -75%;
    transition: height 0s ease;
  }
  .navbar-collapse.show {
    left: 0;
    transition: left 150ms ease-in-out;
    top: 0;
    background: #0F1125;
  }

  .navbar-toggler.collapsed~.navbar-collapse {
    transition: left 100ms ease-in-out;
  }
  header .navbar-nav {
    grid-gap: 15px;
    gap: 15px;
  }
  .navbar-nav .dropdown-menu {
    position: absolute;
  }
  ul.navbar-nav {
    height: 60vh;
    justify-content: center;
  }
  .tabs .nav-tabs .nav-link {
    font-size: 16px;
    font-weight: 500;
    line-height: 16px;
    padding: 15px 15px;
    color: #1B1717;
    border-bottom: 3px solid #F5F5F5;
  }
  section {
    padding-top: 4rem;
    padding-bottom: 4rem;
  }

  .vide-section {
    padding-top: 2rem;
  }
  .blog-post {
    grid-template-columns: auto auto;
    gap: 30px;
  }
}

@media (max-width:767px) {
  .hero-section h1 {
    font-size: 30px;
  }
  .text-card {
    padding: 20px 0;
  }
  .heading-text {
    font-size: 24px;
    padding: 0 20px;
  }
  .blog-wrap {
    max-width: 100%;
  }
  .blog-posts img {
    min-height: 300px;
    object-fit: cover;
  }
  .img-testimonials img {
    max-width: 114px;
  }
  .items-slider-wrap .row {
    flex-direction: column-reverse;
  }
  .items-slider-wrap .col-md-5 {
    transform: translateY(80px);
    position: relative;
    z-index: -1;
  }
  .section-heading {
    margin-bottom: 20px;
    font-size: 28px;
  }
  .our-technologes-items {
    flex-direction: column-reverse;
  }
  .hone-carbon-items {
    background-image: none !important;
  }
  .image-section-hone img {
    transform: scale(1);
  }
  .future-section {
    padding: 4rem 0 !important;
  }
  .pt-100 {
    padding-top: 4rem;
  }
  .desktop-view {
    display: none;
  }
  .mobile-view {
    display: block;
  }
  .section-Features .row {
    flex-direction: column-reverse;
  }
  .flex-direction {
    flex-direction: column-reverse;
    gap: 25px;
  }
  .team-members {
    grid-template-columns: auto auto;
  }
  .entry h2 {
    font-size: 20px;
  }
  .blog-post > div .blog-cart-wrap {
    border: 1px solid #DADADA;
    padding: 18px 15px;
  }
  .read-more {
    margin-top: 45px;
  }
}

@media (max-width:576px) {
  .hero-section h1, .text-slider h1 {
    font-size: 25px;
    font-weight: 900;
  }
  .heading-contact {
    font-size: 35px;
  }
  .how-it-work-item {
    flex-direction: column;
    text-align: center;
  }
  .tabs-heading {
    font-size: 20px;
    line-height: 24px;
  }
  .how-it-work-items::before {
    content: none;
  }
  .how-it-work-item {
    margin-bottom: 30px;
  }
  .features-items img {
    max-width: 23px;
  }
  .text-slider h1 {
    margin-bottom: 15px;
  }
  section {
    padding-top: 2rem;
    padding-bottom: 2rem;
  }
  .tab-content>.tab-pane {
    padding: 20px;
  }
  .paragarph-heading {
    font-size: 18px;
    line-height: 32px;
  }
  .technologes-heading {
    font-size: 21px !important;
  }
  .paragarph {
    font-size: 16px;
  }
  .our-technologes-items .btn {
    width: 100%;
  } 
  .section-award {
    padding: 4rem 0;
  }
  .design-award img {
    max-width: 190px;
  }
  footer {
    background-color: #303246;
    padding: 48px 0 54px;
    color: #fff;
  }
  .copyright, .copyright a {
    font-size: 16px;
  }
  footer h3 {
    font-size: 24px;
  }
  .get-in-touch {
    gap: 30px;
    padding: 24px;
  }
  .team-members {
    grid-template-columns: auto;
  }
  .page-wrapper {
    margin-top: 60px;
  }
  .blog-post {
    grid-template-columns: auto;
  }
  .slick-next {
    right: -12px !important;
  }
  .slick-prev {
    left: -12px !important;
  }
}